/*
*
* ==========================================
* GENERAL
* ==========================================
*
*/
.testimonial {
  position: relative;
  background: url(../img/row-bgimage-3.png) no-repeat;
  background-position: top center;
  background-size: 70%;
}

.testimonial-img {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  position: relative;
}

.testimonial-img::after {
  content: "\f10e";
  width: 30px;
  height: 30px;
  border-radius: 50%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
  -ms-flex-pack: center;
  justify-content: center;
  color: #fff;
  font-family: "Font Awesome\ 5 Free";
  font-weight: 900;
  background: #9055a2;
  position: absolute;
  bottom: 0;
  right: 0;
  font-size: 0.8rem;
}

.accordion a {
  background: #f8f9fa;
}

.accordion a[aria-expanded="true"] {
  background: #9055a2;
  color: #fff;
}

.icon {
  /* width: 50px;
  height: 50px; */
  border-radius: 50%;
  /* border: 2px solid #9055a2; */
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
  justify-content: center;
  -ms-flex-align: center;
  align-items: center;
}

.gmap {
  height: 350px;
}

.btn-outline-light:hover,
.btn-outline-light:focus {
  color: #9055a2 !important;
}

/*
*
* ==========================================
* NAVBAR
* ==========================================
*
*/
.top-bar [class*="col"].bg-primary {
  position: relative;
}

.top-bar [class*="col"].bg-primary::before {
  content: "";
  display: block;
  position: absolute;
  width: 20rem;
  height: 100%;
  top: 0;
  left: -20rem;
  background: #9055a2;
}

/*
*
* ==========================================
* FOOTER
* ==========================================
*
*/
.footer-link {
  color: #6c757d;
  transition: all 0.3s;
}

.footer-link:hover,
.footer-link:focus {
  color: #9055a2;
  text-decoration: none;
}

/*
* ===================================================
*    Custom Orion Icons
* ===================================================
*/
.svg-icon {
  width: 40px;
  height: 40px;
  vertical-align: text-bottom;
  stroke: currentColor;
  stroke-width: 3;
  --layer1: currentColor;
  --layer2: currentColor;
  --layer3: currentColor;
}

.svg-icon.svg-icon-light {
  stroke-width: 2;
}

.svg-icon.svg-icon-heavy {
  stroke-width: 4;
}

.svg-icon-big {
  width: 64px;
  height: 64px;
}

.svg-icon-sm {
  width: 32px;
  height: 32px;
}

